<template>
  <header-page />
  <h1> Welcome to {{ name }} in Update Page</h1>
  <form>
    <label for="name">Name:</label><br />
    <input type="text" id="name" v-model="restorant.name" /><br />
    <label for="email">Address:</label><br />
    <input type="text" id="address" v-model="restorant.address" /><br />
    <label for="password">Contact:</label><br />
    <input type="text" id="contact" v-model="restorant.contact" /><br />
    <button @click.prevent="updateRestorant">Update Restorant</button>
  </form>
</template>

<script>
import HeaderPage from "./Header.vue";
import axios from "axios";
export default {
  name: "UpdatePage",
  data() {
    return {
      name: "",
      restorant: {
        name:'',
        address:'',
        contact:'',
      }
    }
  },
  components: {
    HeaderPage,
  },
  methods: {
    async updateRestorant() {
      const result = await axios.put("http://localhost:3000/restorant/" + this.$route.params.id, {
        name: this.restorant.name,
        address: this.restorant.address,
        contact: this.restorant.contact,
      });
      if (result.status == 200) {
        this.$router.push({name: "Home"});
      }
    }
  },
  async mounted() {
    let users = localStorage.getItem("users-info");
    if (users) {
      this.name = JSON.parse(users).name;
    }
    if (!users) {
      this.$router.push({ name: "SignUp" });
    }
    const result = await axios.get("http://localhost:3000/restorant/" + this.$route.params.id);
    this.restorant = result.data;
  },
};
</script>
