<template>
  <header-page />
  <h1> Welcome to {{ name }} in Home Page</h1>
  <table>
  <tr>
    <th>Id</th>
    <th>Name</th>
    <th>Contact</th>
    <th>Address</th>
    <th>Actions</th>
  </tr>
  <tr v-for="item in restorant" :key="item.id">
    <td>{{ item.id }}</td>
    <td>{{ item.name }}</td>
    <td>{{ item.contact }}</td>
    <td>{{ item.address }}</td>
    <td>
      <router-link :to="'/update/' + item.id">Update</router-link>
      <button @click="deleteRestorant(item.id)">Delete</button>
    </td>
  </tr>
</table>
</template>

<script>
import axios from "axios";
import HeaderPage from "./Header.vue";
export default {
  name: "HomePage",
  data() {
    return {
      name: "",
      restorant: [],
    }
  },
  components: {
    HeaderPage,
  },
  methods: {
    async deleteRestorant(id) {
      let result = await axios.delete("http://localhost:3000/restorant/" + id);
      if (result.status == 200) {
        this.loadData();
      }
    },
    async loadData() {
      let users = localStorage.getItem("users-info");
      if(users) {
        this.name = JSON.parse(users).name;
      }
      if (!users) {
        this.$router.push({ name: "SignUp" });
      }
      let result = await axios.get("http://localhost:3000/restorant");
      this.restorant = result.data;
    },
  },
  async mounted() {
    this.loadData();
  }
};
</script>
<style>
table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

td, th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: #dddddd;
}
</style>